.common {
}
.row {
  display: flex;
  margin: -12px;
}

.col {
  padding: 12px;
  flex: 0 0 33%;
  max-width: 33%;
}
.section {
  background: #ffffff;
  box-shadow: 0px 12px 24px rgba(87, 111, 172, 0.08);
  border-radius: 12px;
  padding: 16px 24px;
  display: flex;
}
.item {
  flex: 0 0 50%;
  max-width: 50%;
  position: relative;

  &:first-child {
    border-right: 1px solid #dde0eb;
    padding: 12px 32px 12px 0;
    .hint {
      right: 12px;
    }
  }

  &:last-child {
    padding: 12px 0 12px 32px;

    .hint {
      right: -12px;
    }
  }
}
.hint {
  position: absolute;
  top: -4px;
  font-size: 0;
}

.name {
  font-size: 14px;
  line-height: 17px;
  color: #a2a7b8;
  margin-bottom: 8px;
}

.value {
  font-size: 28px;
  line-height: 34px;
  color: #1e0e33;
  margin-bottom: 8px;

  &:last-child {
    margin-bottom: 0;
  }
}

.persent {
  border-radius: 4px;
  font-family: Montserrat;
  font-size: 12px;
  line-height: 15px;
  padding: 2px 4px 2px 18px;
  position: relative;
  display: inline-block;

  &--down {
    color: #eb2667;
    background: #fff2f5;
    .arrow {
      transform: translateY(-50%) rotate(180deg);
      fill: #eb2667;
    }
  }

  &--up {
    color: #00a389;
    background: #e8fcf9;

    .arrow {
      transform: translateY(-50%) rotate(0);
      fill: #00a389;
    }
  }
  &--default {
    color: #a2a7b8;
    background: #dde0eb;
    padding-left: 4px;

    .arrow {
      display: none;
    }
  }

  &Value {
  }
}

.arrow {
  position: absolute;
  top: 50%;
  left: 6.5px;
  transform: translateY(-50%);
  transform-origin: 50% 50%;
  transition: 0.3s;
}
